*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  word-wrap: break-word;
}

body {
  background-color: $dark-blue;
  font-family: Helvetica, Arial, sans-serif;
  font-size: $m-size;
  font-style: normal;
  background-color: $main-background;
}

button {
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

a {
  font-size: 1.6rem;
  text-decoration: none;
  color: $link-color;
}

a:hover {
  color: $link-hover-color;
  text-decoration: none;
  font-weight: 600;
}

h3 {
  font-style: normal;
  text-transform: lowercase;
  text-transform: capitalize;
}

// p {
//   font-style: normal;
// }

li {
  font-style: normal;
}

i {
  color: $link-color;
}

a:hover.git-hub {
  font-size: $m-size;
  color: $link-color;
  font-weight: 400;
}

nav a.w3-button {
  color: $link-color;
}

.padding-left {
  padding-left: $xs-size;
}

.style-box-1 {
  display: block;
  width: 100%;
  text-align: center;
}

.style-box-2 {
  display: block;
  width: 100%;
  text-align: left;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 100%;
  }

  .style-container {
    width: 100%;
    padding-top: $xs-size;
    box-shadow: 2px 2px 10px grey;
    margin: $xs-size 0px;
  }

  #google_map {
    width: 80%;
    height: 80%;
  }
}

.w3-card-picture {
  width: 100%;
}

.w3-bar {
  width: 100%;
}

.outer-box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  flex-flow: row;
  padding: 8px 8px;
  margin: 16px 8px 8px;
  flex-basis: 100%;
  height: 55px;
}

.box-1 {
  display: block;
  text-align: center;
  padding-right: 4px;
  flex: 1 1 100px;
  flex-basis: 50px;
  height: 55px;
  order: 1;
}

.icon {
  color: $link-color;
}

.git-hub {
  font-size: $m-size;
  color: $link-color;
  font-weight: 400;
}

.profile {
  color: $profile;
}

.shadow {
  box-shadow: 2px 2px 10px grey;
}

img:hover {
  opacity: 0.9;
  filter: alpha(opacity=100);
}

.resume-bg {
  background-color: $container-backgroundcolor;
}
